import { Drawer, Form } from "antd";
import { useCallback, useEffect, useState } from "react";
import Swal from "sweetalert2";
import { Fn } from "../../../../helper/function";
import { TaskToDoListService } from "../../../../services/taskService";
import { MyFormItem } from "../../../core/Input";
import { MyButton } from "../../../core/MyButton";

export default function ModifyComponent(props: any) {
    const [content, setContent] = useState();
    const [isCheck, setIsCheck] = useState(false);

    const loadData = useCallback(() => {
        if (props.keyData !== undefined) {
            TaskToDoListService.GetDetail(props.projectKey, props.sprintKey, props.keyData, (data: any) => {
                let row = data.data.data;
                setContent(row.content);
                setIsCheck(row.isCheck);
            });
        }
    }, [props.keyData, props.projectKey, props.sprintKey]);

    useEffect(() => {
        loadData();
    }, [loadData]);

    function handleOnClose() {
        props.setVisible(false);
        if (props.setKeyData !== undefined) {
            props.setKeyData(undefined);
        }
    }

    function handleSubmitForm() {
        let body = {
            content: content,
            isCheck: isCheck
        }

        props.setIsLoading(true);
        if (props.keyData !== undefined) {
            edit(body);
        } else {
            add(body);
        }
    }

    function add(body: any) {
        TaskToDoListService.Add(props.projectKey, props.sprintKey, body, (data: any) => {
            Fn.PrintMsgSuccess(data);
            props.loadData();
        }, () => {
            props.setIsLoading(false);
        })
    }

    function edit(body: any) {
        TaskToDoListService.GetDetail(props.projectKey, props.sprintKey, props.keyData, (data: any) => {
            body = Fn.GetTimestamp(data, body);
            TaskToDoListService.Edit(props.projectKey, props.sprintKey, props.keyData, body, (data: any) => {
                Fn.PrintMsgSuccess(data);
                props.loadData();
            }, () => {
                props.setIsLoading(false);
            })
        }, () => {
            props.setIsLoading(false);
        })
    }

    function handleClickDelete() {
        props.setIsLoading(true);
        Swal.fire({
            title: "Xác nhận xóa",
            text: "Bạn có muốn xóa không?"
        }).then((check) => {
            if (check.isConfirmed) {
                doDelete();
            } else {
                props.setIsLoading(false);
            }
        })
    }

    function doDelete() {
        TaskToDoListService.GetDetail(props.projectKey, props.sprintKey, props.keyData, (data: any) => {
            TaskToDoListService.Delete(props.projectKey, props.sprintKey, props.keyData, Fn.GetTimestamp(data), (data: any) => {
                Fn.PrintMsgSuccess(data);
                props.loadData();
                handleOnClose();
            }, () => {
                props.setIsLoading(false);
            })
        }, () => {
            props.setIsLoading(false);
        })
    }

    return (
        <Drawer
            title={props.keyData === undefined ? "Thêm mới" : "Chỉnh sửa"}
            open={props.visible}
            onClose={handleOnClose}>
            <Form
                layout="vertical"
                onSubmitCapture={handleSubmitForm}>
                <MyFormItem.Input
                    label="Nội dung"
                    value={content}
                    setValue={setContent} />

                <MyFormItem.Switch
                    label="Đã hoàn thành"
                    value={isCheck}
                    setValue={setIsCheck} />

                <MyButton
                    htmlType="submit"
                    value={props.keyData === undefined ? "Thêm" : "Chỉnh sửa"} />

                <MyButton value="Xóa"
                    onClick={handleClickDelete} />
            </Form>
        </Drawer>
    );
}